/**
 * 
 */
import React, { Component } from 'react';
import { Form, Radio, Button, Input, DatePicker, InputNumber } from 'antd';
import { validChinese, validIntNumber } from '../../utils/formValidate'
import moment from 'moment';
const FormItem = Form.Item;

class Module2 extends Component {
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, data) => {
            if (err) return;
            //
            data.birthday = data.birthday ? new Date(data.birthday).getTime() : ''
            this.props.onSubmit(data)
        });
    }

    render() {
        let {
            sex,
            age,
            minority,
            birthday,
            job,
            educationDegree,
            incomeLevel,
            phoneLink,
            addressLink
        } = this.props.formData;
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 2 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        return (
            <div>
                <div className="title"></div>
                <Form labelAlign="left" {...formItemLayout} onSubmit={this.handleSubmit.bind(this)} >
                    <FormItem label="">
                        {
                            getFieldDecorator('sex', {
                                initialValue: sex,
                            })(
                                <Radio.Group>
                                    <Radio value={1}></Radio>
                                    <Radio value={2}></Radio>
                                </Radio.Group>
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('age', {
                                initialValue: age,
                                rules: [{
                                    validator: validIntNumber
                                }]
                            })(
                                <Input />
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('minority', {
                                initialValue: minority,
                            })(
                                <Radio.Group>
                                    <Radio value={1}></Radio>
                                    <Radio value={2}></Radio>
                                </Radio.Group>
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('birthday', {
                                initialValue: birthday?moment(birthday):'',
                            })(
                                <DatePicker />
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('job', {
                                initialValue: job,
                                rules: [{
                                    validator: validChinese
                                }]
                            })(
                                <Input></Input>
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('educationDegree', {
                                initialValue: educationDegree,
                            })(
                                <Radio.Group>
                                    <Radio value={1}></Radio>
                                    <Radio value={2}></Radio>
                                    <Radio value={3}></Radio>
                                    <Radio value={4}></Radio>
                                </Radio.Group>
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('incomeLevel', {
                                initialValue: incomeLevel,
                            })(
                                <Radio.Group>
                                    <Radio value={1}>&lt;5000/</Radio>
                                    <Radio value={2}>5000-10000/</Radio>
                                    <Radio value={3}>10000</Radio>
                                </Radio.Group>
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('phoneLink', {
                                initialValue: phoneLink,
                                rules: [{
                                    validator: validIntNumber
                                }]
                            })(
                                <Input></Input>
                            )
                        }
                    </FormItem>
                    <FormItem label="&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;">
                        {
                            getFieldDecorator('addressLink', {
                                initialValue: addressLink,
                            })(
                                <Input></Input>
                            )
                        }
                    </FormItem>
                </Form>
                {
                    this.props.canSave ? <div className="btn-wrap">
                        <Button type="primary" disabled={this.props.disabled} onClick={this.handleSubmit.bind(this)}></Button>
                        <Button onClick={this.props.onCancel}></Button>
                    </div> : null
                }
            </div >
        )
    }
}

const ThisForm = Form.create({
    onValuesChange: (props, changedValues, allValues) => {
        if (!props.canSave) {
            props.setCanSave(true)
        }
    }
})(Module2);

export default ThisForm