/**
 * ABI
 */
import React, { Component } from 'react';
import { Form, Button, Input } from 'antd';
import { validDoubleNumber } from '../../utils/formValidate'

const FormItem = Form.Item;

class Module11 extends Component {

    //
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (err) return;
            //
            this.props.onSubmit(values)
        });
    }

    render() {
        let {
            abiOffside,
            abiLeftside,
        } = this.props.formData;
        const disabled = this.props.disabled;
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 2 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 10 },
            },
        };
        return (
            <div>
                <div className="title">ABI</div>
                <Form labelAlign="left" {...formItemLayout} onSubmit={this.handleSubmit.bind(this)}>
                    <FormItem
                        label=""
                    >
                        {getFieldDecorator('abiOffside', {
                            initialValue: abiOffside,
                            rules: [{
                                validator: validDoubleNumber
                            }]
                        })(
                            <Input></Input>
                        )}

                    </FormItem>
                    <FormItem
                        label=""
                    >
                        {getFieldDecorator('abiLeftside', {
                            initialValue: abiLeftside,
                            rules: [{
                                validator: validDoubleNumber
                            }]
                        })(
                            <Input></Input>
                        )}
                    </FormItem>
                </Form>
                {
                    this.props.canSave ? <div className="btn-wrap">
                        <Button disabled={this.props.disabled} type="primary" onClick={this.handleSubmit.bind(this)}></Button>
                        <Button onClick={this.props.onCancel}></Button>
                    </div> : null
                }
            </div>
        )
    }
}

const ThisForm = Form.create({
    onValuesChange: (props, changedValues, allValues) => {
        if (!props.canSave) {
            props.setCanSave(true)
        }
    }
})(Module11);

export default ThisForm