import React, { Component } from 'react';
import { Icon, Input, Modal, Button, Table, Select, Tabs, message, Empty } from 'antd'
import './styles/patient.css'
import { withRouter } from 'react-router-dom';
import PageHeader from '../../components/PageHeader';
import { createGroup, findGroup, findGroupSelf, updateGroup, deleteGroup, findPatientList } from '../../apis/relation';
import { throttle,buttonAuth } from '../../utils/index'
import {getButton} from '../../apis/user'
const Option = Select.Option;
const TabPane = Tabs.TabPane;

class Patient extends Component {

  state = {
    group: [{
      id: 0,
      value: "",
      topicId: 0
    }],
    currentGroup: "0-0",
    actionGroup: [{
      key: 'newGroup',
      name: ""
    },{
      key: 'followUp',
      name: ""
    }, {
      key: 'warning',
      name: ""
    }, {
      key: 'all',
      name: ""
    }],
    currentAction: 'newGroup',
    groupEditVisible: false,
    waitToAddData: [{
      id: "1",
      name: "",
      mobile: "138000000000",
      remark: ""
    }],
    waitToAddVisible: false,
    showAddBtn: true,
    patientList: [],
    searchList: [],
    groupData: [],
    emptyWords:"",
    buttonKey:[]
  }

  componentWillMount() {
    this.actionGetGroup()
    this.actionFindGroupSelf()
    //
    this.actionGetButton()
  }

  /**
   * 
   */
  handleChangeGroup(key) {
    this.setState({ currentGroup: key })
  }

  /**
   * 
   */
  handleChangeAction(key) {
    let { currentGroup } = this.state;
    let groupId = +currentGroup.split('-')[0]
    let topicId = +currentGroup.split('-')[1]
    if(key==="followUp"){this.setState({emptyWords:""})}
    if(key==="warning"){this.setState({emptyWords:""})}
    if(key==="newGroup"){this.setState({emptyWords:""})}
    if(key==="all"){this.setState({emptyWords:""})}

    this.actionGetPatientList({ groupId, topicId, warningType: key })
    this.setState({ currentAction: key })
  }

  //tab
  handleTabsCallback(key) {
    let groupId = parseInt(key.split('-')[0]);
    let topicId = parseInt(key.split('-')[1])
    let warningType = this.state.currentAction
    this.actionGetPatientList({ groupId, topicId, warningType })
    this.setState({ currentGroup: key })
  }

  handleGroupEditVisible() {
    this.actionFindGroupSelf()
    this.setState({ groupEditVisible: true })
  }

  handleGroupEditHide() {
    this.actionFindGroupSelf()
    this.setState({ groupEditVisible: false })
  }

  handleWaitToAddVisible() {
    this.setState({ waitToAddVisible: true })
  }

  handleWaitToAddHide() {
    this.setState({ waitToAddVisible: false })
  }

  //
  handleGoToArchives(id,tab=1) {
    this.props.history.push('/patient/archives?id=' + id +"&tab="+tab)
  }

  //
  handleSearch(value) {
    let keywords = value
    this.actionSerchPatient({ keywords })
  }

  //
  handleAddGroup() {
    //
    let { groupData } = this.state;
    let groupLen = groupData.length;
    let lastGroupId = 1
    if (groupLen > 0) {
      lastGroupId = groupData[groupLen - 1].groupId + 1
    }
    if (groupLen < 6) {
      let groupItem = { groupName: "", editable: true, groupId: lastGroupId, newAdd: true }
      groupData.push(groupItem)
      this.setState({ groupData, showAddBtn: false })
    }
  }

  //
  handleInput(groupId, e) {
    let groupName = e.target.value
    this.setState({ editGroupName: groupName, editGroupId: groupId || null })
  }

  //
  handleAddGroupItem(groupId) {
    const { editGroupName, editGroupId, groupData } = this.state;
    if(editGroupName.trim().length > 20){
      message.error('')
      return
    }
    if (editGroupName.trim()) {
      for (let i in groupData) {
        if (groupData[i].groupId === editGroupId) {
          if (groupData[i].newAdd) {
            this.actionCreateGroup({ groupName: editGroupName })
          } else {
            this.actionUpdateGroup({ groupName: editGroupName, groupId: editGroupId })
          }
        }
      }
      this.setState({ groupData, showAddBtn: true })
    } else {
      message.error('')
    }
  }

  //
  handleEditable(groupId) {
    let { groupData } = this.state
    let editGroupName = ''
    let editGroupId = ''
    for (let i in groupData) {
      if (groupData[i].groupId === groupId) {
        groupData[i].editable = true;
        editGroupName = groupData[i].groupName
        editGroupId = groupData[i].groupId
      }
    }
    this.setState({ groupData, showAddBtn: false, editGroupName, editGroupId })
  }

  //
  handleDeleteGroup(groupId) {
    this.actionDeleteGroup({ groupId })
  }

  //
  handleSearchChange(value) {
    this.props.history.push('/patient/archives?id=' + value)
  }

  //
  handleJumpToChat(patientId){
    this.props.history.push('/chat?id='+patientId)
  }

  /**
   * 
   * @param {*} data 
   */
  async actionCreateGroup(data) {
    let group = await createGroup(data)
    if (group && group.code === 200) {
      this.actionGetGroup()
      message.success('')
      this.actionFindGroupSelf()
    }
  }

  /**
   * 
   */
  async actionGetGroup() {
    let allGroup = [{
      id: 0,
      value: "",
      topicId: 0
    }]
    let group = await findGroup()
    let list = group.data.groups || []
    let groupDataLen = list.length
    if (groupDataLen > 0) {
      this.actionGetPatientList({ groupId: list[0].id, topicId: list[0].topicId, warningType: "newGroup" })
      this.setState({
        group: list.concat(allGroup),
        currentGroup: list.concat(allGroup)[0].id + "-" + list.concat(allGroup)[0].topicId,//tabsgroupIdtopicId
      })
    }else{
      this.actionGetPatientList({ groupId: 0, topicId: 0, warningType: "newGroup" })
    }
  }

  /**
   * 
   * @param {*} data 
   */
  async actionUpdateGroup(data) {
    let group = await updateGroup(data).catch(err => this.actionFindGroupSelf())
    if (group && group.code === 200) {
      this.actionGetGroup()
      this.actionFindGroupSelf()
      message.success('')
    }
  }

  async actionDeleteGroup(data) {
    let group = await deleteGroup(data)
    if (group && group.code === 200) {
      this.actionGetGroup()
      this.actionFindGroupSelf()
      message.success('')
    }
  }

  /**
   * 
   */
  async actionGetPatientList(data) {
    let list = await findPatientList(data)
    this.setState({ patientList: list.data.patientCards })
  }

  /**
   * 
   * @param {*} data 
   */
  async actionSerchPatient(data) {
    let list = await findPatientList(data)
    this.setState({ searchList: list.data.patientCards })
  }

  /**
   * 
   */
  async actionFindGroupSelf() {
    let selfGroup = await findGroupSelf()

    let showAddBtn = true
    //
    let list = selfGroup.data.groups || []
    for (let i in list) {
      list[i].editable = false
    }
    let groupDataLen = list.length
    if (groupDataLen >= 6) {
      showAddBtn = false
    }
    this.setState({
      groupData: list,
      showAddBtn
    })
  }

  //
  async actionGetButton(){
    let buttons = await getButton({pageId:2})
    let buttonList = buttons.data.buttons
    let buttonKey = buttonList.map(item => item.buttonKey)
    this.setState({buttonKey})
  }

  render() {
    const { group, currentGroup, actionGroup, currentAction, groupEditVisible, showAddBtn, patientList, searchList, groupData ,emptyWords,buttonKey} = this.state;
    const editGroupColumns = [{
      title: '',
      width: 80,
      align: 'center',
      render:(row,record,index)=>index+1
    }, {
      title: '',
      key: 'groupName',
      render: row => {
        return (
          <Input
            defaultValue={row.groupName}
            onChange={this.handleInput.bind(this, row.groupId)}
            disabled={!row.editable}
          />
        )
      }
    }, {
      title: '',
      dataIndex: 'groupPatientQty',
      key: 'groupPatientQty',
      align: 'center'
    }, {
      title: '',
      align: 'center',
      render: row => {
        if (row.editable) {
          if (row.deleteFlag) {
            return (
              <span>
                <span className="edit-btn" onClick={this.handleAddGroupItem.bind(this, row.groupId)}></span>
                {buttonAuth(buttonKey,'deleteGroup',<span className="delete-btn" onClick={this.handleDeleteGroup.bind(this, row.groupId)}></span>)}
              </span>
            )
          }
          return (<span className="edit-btn" onClick={this.handleAddGroupItem.bind(this, row.groupId)}></span>)
        } else {
          if (row.deleteFlag) {
            return (
              <span>
                {buttonAuth(buttonKey,'updateGroup',<span className="edit-btn" onClick={this.handleEditable.bind(this, row.groupId)}></span>)}
                {buttonAuth(buttonKey,'deleteGroup',<span className="delete-btn" onClick={this.handleDeleteGroup.bind(this, row.groupId)}></span>)}
              </span>
            )
          }
          return buttonAuth(buttonKey,'updateGroup',<span className="edit-btn" onClick={this.handleEditable.bind(this, row.groupId)}></span>)
        }
      }
    }];

    //
    const actionItem = actionGroup.map((item, index) => {
      return (
        <span
          className={currentAction === item.key ? "action-item current-action" : "action-item"}
          key={index}
          onClick={this.handleChangeAction.bind(this, item.key)}
        >
          {item.name}
        </span>
      )
    })
    //
    const groupItem = group.map((item) => <TabPane tab={item.value} key={item.id + "-" + item.topicId} >{actionItem}</TabPane>)


    const waitToAddColumns = [{
      title: "",
      dataIndex: "num",
      key: "num",
    }, {
      title: "",
      dataIndex: "name",
      key: "name"
    }, {
      title: "",
      dataIndex: "mobile",
      key: "mobile"
    }, {
      title: "",
      dataIndex: "remark",
      key: "remark"
    }, {
      title: "",
      render: row => (
        <span>
          <span className="edit-btn"></span>
          <span className='delete-btn'></span>
        </span>
      )
    }]

    const options = searchList.map(d => <Option key={d.patientId} value={d.patientId}>{d.name}</Option>);

    //
    const patientItem = patientList.map((item, index) => (
      <div key={index} className='patient'>
        <div className='patient-top' onClick={this.handleGoToArchives.bind(this, item.patientId || '')}>
          <div className="name">{item.realName || ''}</div>
        </div>
        <div className="sub-info" onClick={this.handleGoToArchives.bind(this, item.patientId || '')}>
          <span>{item.age || 0}</span>
          {item.sex !== '' && item.sex === "" ? <Icon type="man" /> : <Icon type="woman" />}
        </div>
        <div className='patient-bottom'>
          {item.warningFlag?<span title="" onClick={this.handleGoToArchives.bind(this, item.patientId,2)}></span>:null}
          <Icon type="message" onClick={this.handleJumpToChat.bind(this, item.patientId || '')}/>
        </div>
      </div>
    ))

    const button = (
      <span
        className="edit-group-icon"
        onClick={this.handleGroupEditVisible.bind(this)}
      >
        <Icon type="form" />&nbsp;
      </span>
    )

    const search = (
      <Select
        style={{ width: 200 }}
        showSearch
        value={this.state.value}
        placeholder=""
        defaultActiveFirstOption={false}
        showArrow={false}
        filterOption={false}
        onSearch={throttle(this.handleSearch.bind(this), 1000)}
        onChange={this.handleSearchChange.bind(this)}
      >
        {options}
      </Select>
    )

    const tabBarExtra = () => (
      <div className='patient-group-right'>
        {buttonAuth(buttonKey,'findGroups', button)}
        {buttonAuth(buttonKey,'findGroups', search)}
      </div>
    )

    //
    const addButton = (
      <div className="add-group-icon">
        {showAddBtn ? (<Button icon="plus" type="primary" onClick={this.handleAddGroup.bind(this)}></Button>) : null}
      </div>
    )

    return (
      <div className="patient-content">
        <PageHeader title="" />
        <Tabs
          type="card"
          activeKey={currentGroup}
          tabBarExtraContent={tabBarExtra()}
          onChange={this.handleTabsCallback.bind(this)}
        >
          {groupItem}
        </Tabs>

        {/*  */}
        {buttonAuth(buttonKey,'findPatientCards',<>{patientList.length === 0 ? <Empty description={emptyWords} style={{ marginTop: "100px" }} /> : <div className="patient-list-wrap">{patientItem}</div>}</>)}
      
        {/** */}
        <Modal
          visible={groupEditVisible}
          title=""
          onCancel={this.handleGroupEditHide.bind(this)}
          footer={null}
          width={700}
        >
          <Table
            columns={editGroupColumns}
            dataSource={groupData}
            pagination={false}
            rowKey={record => record.groupId}
          />
          {buttonAuth(buttonKey,'createGroup',addButton)}
        </Modal>

        {/**  */}
        {/* <Modal
          visible={waitToAddVisible}
          title=""
          onCancel={this.handleWaitToAddHide.bind(this)}
          footer={null}
          width={700}
        >
          <Table 
            columns={waitToAddColumns} 
            dataSource={waitToAddData} 
            pagination={false}
            rowKey={record => record.id}
          />
        </Modal> */}

      </div>
    );
  }
}

export default withRouter(Patient)