import request from '../utils/request'
const SERVICE_NAME = '/rpmrelation_service'

/**
 * 
 * @param {*} data 
 */
const bindPatient = (data) => {
    return request({
        url: SERVICE_NAME + "/bindPatient",
        data,
        method: "post"
    })
}

/**
 * 
 */
const findGroup = () => {
    return request({
        url: SERVICE_NAME + '/doctorGroupSelect',
        method: "post"
    })
}

/**
 * 
 */
const findGroupSelf = () => {
    return request({
        url: SERVICE_NAME + "/findGroups",
        method: "post"
    })
}

/**
 * 
 * @param {*} data 
 */
const createGroup = (data) => {
    return request({
        url: SERVICE_NAME + '/createGroup',
        method: "post",
        data
    })
}

/**
 *
 */
const updateGroup = (data) => {
    return request({
        url: SERVICE_NAME + '/updateGroup',
        method: "post",
        data
    })
}

/**
 * 
 * @param {*} data 
 */
const deleteGroup = (data) => {
    return request({
        url: SERVICE_NAME + '/deleteGroup',
        method: "post",
        data
    })
}

/**
 * 
 * @param {*} data 
 */
const findPatientList = (data) => {
    return request({
        url: SERVICE_NAME + '/findPatientCards',
        data,
        method: "post"
    })
}

/**
 * 
 * @param {*} data 
 */
const findMedicalRecord = (data) => {
    return request({
        url: SERVICE_NAME + '/findTreatmentRecord',
        data,
        method: "post"
    })
}

/**
 * 
 * @param {*} data 
 */
const findPatient = (data) => {
    return request({
        url: SERVICE_NAME + '/getDoctorPatient',
        data,
        method: "post"
    })
}

/**
 * 
 */
const groupSelectInfo = () => {
    return request({
        url: SERVICE_NAME + '/groupSelectInfo',
        method: "post"
    })
}

export {
    bindPatient,
    findGroup,
    createGroup,
    updateGroup,
    deleteGroup,
    findPatientList,
    findMedicalRecord,
    findPatient,
    findGroupSelf,
    groupSelectInfo
}