import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import {Button,Tabs} from 'antd'
import PageHeader from '../../components/PageHeader';
import {DataTable,DataChart,Measurement,BaseInfo,MedicalRecord,Followup} from './components/index'
import { findPatient} from '../../apis/relation';
import {getQueryString, setLocal, getLocal,buttonAuth} from '../../utils/index';
import {getButton} from '../../apis/user'
import moment from 'moment'

import "./styles/archives.css"

const TabPane = Tabs.TabPane;


class Plan extends Component {
  state={
    tab2PageType:"chart",
    patientId:0,
    patientInfo:{},
    currentType:"1",
    buttonKey:[]
  }

  componentWillMount(){
    let patientId = this.props.patientId || parseInt(getQueryString('id',this.props.location.search)) || this.props.patientId
    //
    let archivesTab =getQueryString('tab',this.props.location.search) ||  getLocal('archivesTab') || "1"
    if(patientId){
      this.setState({patientId,currentType:archivesTab})
      this.actionFindPatient({patientId})
    }
    this.actionGetButton({pageId:2})
  }

  //
  handleTabsCallback(value){
    setLocal('archivesTab',value.toString())
  }

  handleTab2ChangePageType(type){
    this.setState({tab2PageType:type})
  }

  handleHeaderBack(){
    this.props.history.goBack()
  }

  //
  handleUpdateSuccess(){
    let {patientId} = this.state
    this.actionFindPatient({patientId})
  }

  //
  handleJumpToChat(){
    let {patientId} = this.state
    this.props.history.push('/chat?id='+patientId)
  }

  /**
   * 
   * @param {*} data 
   */
  async actionFindPatient(data){
    let patient = await findPatient(data)
    this.setState({patientInfo:patient.data || {}})
  }

  //
  async actionGetButton(){
    let buttons = await getButton({pageId:2})
    let buttonList = buttons.data.buttons
    let buttonKey = buttonList.map(item => item.buttonKey)
    this.setState({buttonKey})
  }

  render() {
    const {tab2PageType,patientId,patientInfo,currentType,buttonKey} = this.state;
    
    const userBaseInfo = () =>(
      <div className="base-info">
        <i className="avatar">
          <img src={patientInfo.headUrl || 'http://c.hiphotos.baidu.com/image/pic/item/a5c27d1ed21b0ef4b9e8896ad3c451da81cb3e85.jpg'} alt=''/>
        </i>
        <i className="name">{patientInfo.realName}</i>
        {patientInfo.sex?<i className='gender'>{patientInfo.sex}</i>:null}
        <i>{patientInfo.age}</i>
        <i>{patientInfo.mobile}</i>
        <i>{patientInfo.groupName || ''}</i>
        <i>{patientInfo.subGroupName || ''}</i>
        {patientInfo.patientNo?<i>{patientInfo.patientNo}</i>:null}
        <i>{moment(patientInfo.enterGroupTime).format('YYYY-MM-DD')}</i>
        {buttonAuth(buttonKey,'sendMsg',<Button type="primary" onClick={this.handleJumpToChat.bind(this)}></Button>)}
      </div>
    )

    const tab2 = () => (
      <div className='tab2'>
        <div className='tab2-header'>
            {tab2PageType === 'chart'?(
              <Button type="primary" onClick={this.handleTab2ChangePageType.bind(this,'table')}></Button>
            ):(
              <Button type="primary" onClick={this.handleTab2ChangePageType.bind(this,'chart')}></Button>
            )}
        </div>
        {tab2PageType === 'chart' ? <DataChart patientId={patientId}/> : <DataTable patientId={patientId} />}
      </div>
    )
    return (
      <div className="archives-wrap">
        <PageHeader title="" onBack={this.handleHeaderBack.bind(this)}/>
        {userBaseInfo()}
        <Tabs 
          defaultActiveKey={currentType} 
          onChange={this.handleTabsCallback.bind(this)}
          type="card"
        >
          {buttonAuth(buttonKey,'getPatientFollow',<TabPane tab="" key="1"><Followup patientId={patientId}/></TabPane>)}
          {buttonAuth(buttonKey,'getPatientDeviceData',<TabPane tab="" key="2">{tab2()}</TabPane>)}
          {buttonAuth(buttonKey,'findTreatmentRecord',<TabPane tab="" key="3"><MedicalRecord patientId={patientId}/></TabPane>)}
          {buttonAuth(buttonKey,'getPatientMeasure',<TabPane tab="" key="4"><Measurement patientId={patientId}/></TabPane>)}
          {buttonAuth(buttonKey,'getDoctorPatient',<TabPane tab="" key="5"><BaseInfo patientInfo={patientInfo} onUpdateSuccess={this.handleUpdateSuccess.bind(this)}/></TabPane>)}
        </Tabs>
      </div>
    );
  }
}

export default withRouter(Plan)