const switchEnum = (id, arrayName) => {
    for (let i in enumObj[arrayName]) {
        if (parseInt(id) === enumObj[arrayName][i].key) {
            return enumObj[arrayName][i].value
        }
    }
}

export {
    enumObj,
    switchEnum
}