/**
 * 
 */
import React, { Component } from 'react';
import { Form, Radio, Button, Input } from 'antd';
import { validDoubleNumber } from '../../utils/formValidate'
const FormItem = Form.Item;

class Module4 extends Component {
    //
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (err) return;
            //
            this.props.onSubmit(values)
        });
    }

    render() {
        let {
            cervicalThickness,
            arterialPlaqueFlag,
            arteriosclerosisFlag,
            arterialStenosisFlag,
            arterialStenosisPercent
        } = this.props.formData;
        const { getFieldDecorator, getFieldValue } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 10 },
            },
        };
        return (
            <div>
                <div className="title"></div>
                <Form labelAlign="left" {...formItemLayout} onSubmit={this.handleSubmit.bind(this)}>
                    <FormItem label="">
                        {
                            getFieldDecorator('cervicalThickness', {
                                initialValue: cervicalThickness,
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonBefore="" addonAfter="mm" />
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('arterialPlaqueFlag', {
                                    initialValue: arterialPlaqueFlag,
                                })(
                                    <Radio.Group>
                                        <Radio value={false}></Radio>
                                        <Radio value={true}></Radio>
                                    </Radio.Group>
                                )
                            }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('arteriosclerosisFlag', {
                                    initialValue: arteriosclerosisFlag,
                                })(
                                    <Radio.Group>
                                        <Radio value={false}></Radio>
                                        <Radio value={true}></Radio>
                                    </Radio.Group>
                                )
                            }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('arterialStenosisFlag', {
                                    initialValue: arterialStenosisFlag,
                                })(
                                    <Radio.Group>
                                        <Radio value={false}></Radio>
                                        <Radio value={true}></Radio>
                                    </Radio.Group>
                                )
                            }
                            {
                                getFieldValue('arterialStenosisFlag') ?
                                    <FormItem className="inline-item">
                                        {
                                            getFieldDecorator('arterialStenosisPercent', {
                                                initialValue: arterialStenosisPercent,
                                                rules:[{
                                                    validator:validDoubleNumber
                                                }]
                                            })(
                                                <Input addonAfter="%" className="cover-input" />
                                            )
                                        }
                                    </FormItem> : null
                            }
                        </FormItem>
                    </FormItem>
                </Form>
                {
                    this.props.canSave ? <div className="btn-wrap">
                        <Button disabled={this.props.disabled} type="primary" onClick={this.handleSubmit.bind(this)}></Button>
                        <Button onClick={this.props.onCancel}></Button>
                    </div> : null
                }
            </div>
        )
    }
}

const ThisForm = Form.create({
    onValuesChange:(props, changedValues, allValues) => {
        if(!props.canSave){
            props.setCanSave(true)
        }
    }
})(Module4);

export default ThisForm