import request from '../utils/request'
import config from '../configs/index'
const SERVICE_NAME = '/rpmaccount_service'

/**
 * 
 * @param {*} data 
 */
const login = (data) => {
  return request({
    url: SERVICE_NAME + '/common/login',
    method: 'post',
    data: {
      appType: config.appType,
      ...data
    }
  })
}

/**
 * 
 */
const logout = () => {
  return request({
    url: SERVICE_NAME + '/common/logout'
  })
}

/**
 * 
 */
