import request from '../utils/request'
const SERVICE_NAME = '/doctor_service'

/**
 * 
 * @param {*} data 
 */
const createGroup = (data) => {
    return request({
        url: SERVICE_NAME + '/admin/create_group',
        data,
        method: "post"
    })
}

/**
 * 
 */
const getGroup = () => {
    return request({
        url: SERVICE_NAME + '/admin/find_group',
        method: "post"
    })
}

/**
 * 
 * @param {*} data 
 */
const updateGroup = (data) => {
    return request({
        url: SERVICE_NAME + '/admin/update_group',
        data,
        mrthod: "post"
    })
}

/**
 * 
 * @param {*} groupId 
 */
const deleteGroup = (groupId) => {
    return request({
        url: SERVICE_NAME + '/admin/delete_group',
        data: {
            groupId
        },
        method: "post"
    })
}

/**
 * 
 * @param {*} data 
 */
const createPatient = (data) => {
    return request({
        url: SERVICE_NAME + '/admin/create_patient',
        data,
        method: "post"
    })
}


/**
 * 
 * @param {*}  patientId id
 */
const checkPatientInTopic = (patientId) => {
    return request({
        url: '/rpmrelation_service/checkPatientInTopic',
        method: 'post',
        data: {
            patientId
        }
    })
}

export {
    createGroup,
    getGroup,
    updateGroup,
    deleteGroup,
    createPatient,
    checkPatientInTopic
}