/**
 * 1
 */
import React, { Component } from 'react';
import { Form, Button, Input, Table, Checkbox } from 'antd';
import { validDoubleNumber } from '../../utils/formValidate'
const FormItem = Form.Item;

class Module4 extends Component {
    //
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (err) return;
            //

            values.urAlbumen = values.urAlbumen_plus[0] == 1 ? values.urAlbumen + '-' : values.urAlbumen + '+';
            values.urKetoneBody = values.urKetoneBody_plus[0] == 1 ? values.urKetoneBody + '-' : values.urKetoneBody + '+';
            values.urRbc = values.urRbc_plus[0] == 1 ? values.urRbc + '-' : values.urRbc + '+';
            values.urSugar = values.urSugar_plus[0] == 1 ? values.urSugar + '-' : values.urSugar + '+';
            values.urWbc = values.urWbc_plus[0] == 1 ? values.urWbc + '-' : values.urWbc + '+';
            delete values.urWbc_plus
            delete values.urSugar_plus
            delete values.urRbc_plus
            delete values.urKetoneBody_plus
            delete values.urAlbumen_plus
            this.props.onSubmit(values)
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const renderContent = (value, row, index) => {
            const obj = {
                children: value,
                props: {},
            };
            if (index > 2 && index < 8) {
                obj.children = <div>
                    <FormItem>
                        {
                            getFieldDecorator(value, {
                                initialValue: this.props.formData[value] ? this.props.formData[value].slice(0, -1) : '',
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input className="middle-input" />
                            )
                        }
                        <span>+</span>
                        {
                            getFieldDecorator(value + '_plus', {
                                initialValue: this.props.formData[value] && this.props.formData[value].indexOf('-') >= 0 ? [1] : []
                            })(
                                <Checkbox.Group>
                                    <Checkbox value={1}>-</Checkbox>
                                </Checkbox.Group>
                            )
                        }
                    </FormItem>
                </div>
            } else {
                obj.children = <FormItem>
                    {
                        getFieldDecorator(value, {
                            initialValue: this.props.formData[value],
                            rules:[{
                                validator:validDoubleNumber
                            }]
                        })(
                            <Input />
                        )
                    }
                </FormItem>
            }
            return obj;
        };
        const columns = [{
            title: '',
            colSpan: 0,
            dataIndex: 'time',
            render: (text, row, index) => {
                let rowSpan = 1;
                // if (index < 4) {
                //     return text;
                // }
                if (index < 100) {
                    rowSpan = 0;
                }
                if (index == 0) {
                    rowSpan = 3;
                }
                if (index == 3) {
                    rowSpan = 5;
                }
                if (index == 8) {
                    rowSpan = 8
                }
                if (index == 16) {
                    rowSpan = 4
                }
                if (index == 20) {
                    rowSpan = 4
                }
                if (index == 24) {
                    rowSpan = 2
                }

                return {
                    children: text,
                    props: {
                        rowSpan: rowSpan,
                    },
                };
            },
        }, {
            title: '',
            colSpan: 2,
            dataIndex: 'key1',
        }, {
            title: '',
            dataIndex: 'key2',
            render: renderContent
        }, {
            title: '',
            dataIndex: 'key3',
        }];
        const data = [{
            key: '1',
            time: '',
            key1: 'Hb',
            key2: 'brHb',
            key3: 'g/L'
        }, {
            key: '2',
            time: '',
            key1: 'WBC',
            key2: 'brWbc',
            key3: <span>10<sup>9</sup>/L</span>
        }, {
            key: '3',
            time: '',
            key1: 'PLT',
            key2: 'brPlt',
            key3: <span>10<sup>9</sup>/L</span>
        }, {
            key: '4',
            time: '',
            key1: '',
            key2: 'urAlbumen',
            key3: ''
        }, {
            key: '5',
            time: '',
            key1: '',
            key2: 'urSugar',
            key3: ''
        }, {
            key: '6',
            time: '',
            key1: '',
            key2: 'urKetoneBody',
            key3: ''
        }, {
            key: '7',
            time: '',
            key1: '',
            key2: 'urRbc',
            key3: ''
        }, {
            key: '8',
            time: '',
            key1: '',
            key2: 'urWbc',
            key3: ''
        }, {
            key: '9',
            time: '',
            key1: 'ALT/ GPT',
            key2: 'ccAlt',
            key3: 'U/L'
        }, {
            key: '10',
            time: '',
            key1: 'AST / GOT',
            key2: 'ccAst',
            key3: 'U/L'
        }, {
            key: '11',
            time: '',
            key1: 'GGT-',
            key2: 'ccGgt',
            key3: 'U/L'
        }, {
            key: '12',
            time: '',
            key1: 'TB',
            key2: 'ccTb',
            key3: 'mmol/L'
        }, {
            key: '13',
            time: '',
            key1: 'ALB',
            key2: 'ccAlb',
            key3: 'g/L'
        }, {
            key: '14',
            time: '',
            key1: 'BUN',
            key2: 'ccBun',
            key3: 'mmol/L'
        }, {
            key: '15',
            time: '',
            key1: 'Cr',
            key2: 'ccCr',
            key3: 'mol/L'
        }, {
            key: '16',
            time: '',
            key1: 'UA',
            key2: 'ccUa',
            key3: 'mol/L'
        }, {
            key: '17',
            time: '',
            key1: 'TG',
            key2: 'blpTg',
            key3: 'mmol/L'
        }, {
            key: '18',
            time: '',
            key1: 'CHOL',
            key2: 'blpChol',
            key3: 'mmol/L'
        }, {
            key: '19',
            time: '',
            key1: 'LDL-c',
            key2: 'blpLdl',
            key3: 'mmol/L'
        }, {
            key: '20',
            time: '',
            key1: 'HDL-c',
            key2: 'blpHdl',
            key3: 'mmol/L'
        }, {
            key: '21',
            time: '',
            key1: 'FPG',
            key2: 'bsFpg',
            key3: 'mmol/L'
        }, {
            key: '22',
            time: '',
            key1: 'PPG2h',
            key2: 'bsPpg',
            key3: 'mmol/L'
        }, {
            key: '23',
            time: '',
            key1: 'HbA1c',
            key2: 'bsGh',
            key3: '%'
        }, {
            key: '24',
            time: '',
            key1: '',
            key2: 'bsGa',
            key3: '%'
        }, {
            key: '25',
            time: '',
            key1: 'FFA',
            key2: 'tnfFfa',
            key3: 'mol/L'
        }, {
            key: '26',
            time: '',
            key1: 'hs-CRP',
            key2: 'tnfCrp',
            key3: 'mmol/L'
        }];

        return (
            <div>
                <div className="title"></div>
                <Form layout="inline" onSubmit={this.handleSubmit.bind(this)}>
                    <Table columns={columns} dataSource={data} bordered pagination={false} />
                    {
                        this.props.canSave ? <div className='btn-wrap'>
                            <FormItem>
                                <Button disabled={this.props.disabled} type="primary" htmlType="submit"></Button>
                                <Button onClick={this.props.onCancel}></Button>
                            </FormItem>
                        </div> : null
                    }
                </Form>
            </div>
        )
    }
}

const ThisForm = Form.create({
    onValuesChange:(props, changedValues, allValues) => {
        if(!props.canSave){
            props.setCanSave(true)
        }
    }
})(Module4);

export default ThisForm