import React, { Component } from 'react';
import { Layout, Form, Breadcrumb, Icon, Modal, Input, Radio, Button, Alert, message, Row, Col } from 'antd';
import { Link } from 'react-router-dom';
import { formItemLayout, tailFormItemLayout } from '../../utils/formItemLayout';
import MyMenu from '../../components/MyMenu.jsx';
import { getLocal } from '../../utils/index';
import { logout } from '../../apis/user';
import { bindPatient } from '../../apis/relation';
import { delCookie ,buttonAuth} from '../../utils/index';
import { isPhoneNumber, isPersonName } from '../../utils/validate';
import './styles/layout.css';
import defaultUser from '../../assets/images/default-user.jpg';
import { withRouter } from 'react-router-dom';
import {findGroup,createGroup ,groupSelectInfo} from '../../apis/relation';

const { Header, Content, Sider } = Layout;
const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const RadioButton = Radio.Button; 
const { TextArea } = Input; 


class MyLayoutForm extends Component {
  state = {
    collapsed: true,
    visible: false,
    addPatientVisible: false,
    groupId: 0,
    subGroupId: 1,
    submitDisabled: false,
    errorMessage: null,
    name: "",
    addModalState: 0,
    wxAddWords: " xxxx XXXX    YU1EJJK5671   http://lifesense.cn/h.3341sm=0187",
    userItem: false,
    userCenterVisible: false,
    updatePhoneVisible: false,
    changePasswordVisible: false,
    addSubmitLoading: false,
    customizeGroup:[],
    classesGroup:[],
    showCustomize:false,
    addState:false,
    newGroupName:"",
    addBtnState:true,
    customizeAdd:false
  };

  componentWillMount() {
    let user = JSON.parse(getLocal("user"))
    this.setState({user})
  }

  onCollapse = (collapsed) => {
    this.setState({
      collapsed
    });
  }

  onClose = () => {
    this.setState({
      visible: false,
    });
  };

  /**
   * 
   */
  handleLogout = () => {
    logout().then(res => {
      delCookie("accessToken")
      delCookie("session")
      window.location.href = '/rpm/#/login'
    })
  }

  handleAddPatientVisible() {
    //
    this.actionFindGroup()
    this.setState({ addPatientVisible: true })
  }

  handleAddPatientHide() {
    let self = this
    this.setState({ addPatientVisible: false })
    setTimeout(() => {
      self.setState({
        addModalState: 0,
        realName: "",
        mobile: "",
        treatmentRemark:"",
        errorMessage:null
      })
    })
  }

  handleSelectGroup(e) {
    if(e.target.value === 0){
      this.setState({ 
        showCustomize:true
      })
    }else{
      let groupId = e.target.value.split('-')[0]
      let topicId = e.target.value.split('-')[1]
      this.setState({ 
        groupId,
        topicId,
        showCustomize:false
      });
    }
  }

  //
  handleSelectGroup2(e){
    this.setState({ 
      groupId: +e.target.value ,
      topicId:0
    })
  }

  /**
   * 
   */
  handleInput(key, e) {
    this.setState({ [key]: e.target.value })
  }

  /**
   * 
   */
  handleFocusInput() {
    this.setState({ errorMessage: null })
  }

  /**
   * 
   */
  handleBlurInput(key, e) {
    switch (key) {
      case "mobile":
        if (!isPhoneNumber(e.target.value)) this.setState({ errorMessage: '' })
        break;
      case "realName":
        if (!isPersonName(e.target.value)) this.setState({ errorMessage: '' })
        break;
      default:
        return
    }
  }

  /**
   * modal0-1-
   * @param {*} state 
   */
  handleChangeAddState(state) {
    this.setState({ addModalState: state })
  }

  /**
   * 
   */
  handleSubmit() {
    const { realName, mobile, groupId, topicId, treatmentRemark } = this.state
    this.actionBindPatient({ realName, mobile, groupId, topicId, treatmentRemark })
  }

  handleShowUserCenter() {
    this.setState({ userItem: true })
  }

  handleHideUserCenter() {
    this.setState({ userItem: false })
  }

  handleUserCenterHide() {
    this.setState({ userCenterVisible: false })
  }

  handleUserCenterVisible() {
    //this.setState({userCenterVisible:true})
    this.props.history.push('/user')
    //this.setState({userCenterVisible:true})
  }

  handleUpdatePhone() {
    this.setState({ updatePhoneVisible: true })
  }

  handleUpdatePhoneHide() {
    this.setState({ updatePhoneVisible: false })
  }

  handleChangePasswordHide() {
    this.setState({ changePasswordVisible: false })
  }

  handleChangePassword() {
    this.setState({ changePasswordVisible: true })
  }

  /**
   * 
   */
  handleGetCode() {

  }

  //
  handleShowAddBox(){
    this.setState({addState:true,addBtnState:false})
  }


  handleNewGroupName(e){
    this.setState({newGroupName:e.target.value})
  }

  //
  handleAddGroup(){
    let {newGroupName} = this.state;
    this.actionCreateGroup({ groupName: newGroupName })
  }

  handleCancelAddGroup(){
    this.setState({addState:false,addBtnState:true})
  }


  /**
   * 
   * @param {*} data 
   */
  async actionCreateGroup(data) {
    let group = await createGroup(data)
    if (group && group.code === 200) {
      message.success('')
      this.actionFindGroup();
      this.setState({addState:false})
    }
  }
  /**
   * 
   * @param {*} data 
   */
  async actionBindPatient(data) {
    this.setState({ addSubmitLoading: true })
    let patient = await bindPatient(data).catch(err => {
      this.setState({ errorMessage: err.msg, addSubmitLoading: false })
    })
    if (patient && patient.code === 200) {
      this.setState({
        addSubmitLoading: false,
        addModalState: 2
      })
    }
  }

  /**
   * 
   */
  async actionFindGroup(){
    let group = await groupSelectInfo()
    if(group && group.code === 200){
      let groups = group.data.nodes
      let showSelf = group.data.showSelf || false
      let classesGroup = []
      let customizeGroup=[]
      //
      this.setState({customizeAdd:showSelf})
      for(let i in groups){
        if(groups[i].topicId === 0){
          //
          customizeGroup.push(groups[i])
        }else{
          classesGroup.push(groups[i])
        }
      }
      this.setState({customizeGroup,classesGroup})
    }
  }

  render() {
    const {
      addPatientVisible,submitDisabled, errorMessage, realName, mobile,
      addModalState, wxAddWords, userItem, userCenterVisible, changePasswordVisible,
      updatePhoneVisible, user, addSubmitLoading,customizeGroup,classesGroup,showCustomize,addState,
      addBtnState,customizeAdd
    } = this.state
    let moreBtn = true
    const showErrorMessage = () => (
      errorMessage ? <Alert message={errorMessage} type="error" /> : null
    )

    const classesItem = classesGroup.map((item,index)=><Radio key={index} value={item.id+"-"+item.topicId}>{item.value}</Radio>)
    const customizeItem = customizeGroup.map((item,index)=><RadioButton key={index} value={item.id}>{item.value}</RadioButton>)
    if(classesItem.length + customizeItem.length >= 6){
      moreBtn=false
    }
    //
    const sigleAdd = () => (
      <div >
        <FormItem  {...formItemLayout} label="">
          <Input
            placeholder=""
            onChange={this.handleInput.bind(this, 'realName')}
            onBlur={this.handleBlurInput.bind(this, 'realName')}
            onFocus={this.handleFocusInput.bind(this)}
            value={realName}
          />
        </FormItem>
        <FormItem  {...formItemLayout} label="">
          <Input
            placeholder=""
            onChange={this.handleInput.bind(this, 'mobile')}
            onBlur={this.handleBlurInput.bind(this, 'mobile')}
            onFocus={this.handleFocusInput.bind(this)}
            value={mobile}
          />
        </FormItem>
        <FormItem  {...formItemLayout} label="">
          <RadioGroup onChange={this.handleSelectGroup.bind(this)}>
            {classesItem}
            {customizeAdd?<Radio value={0}></Radio>:null}
          </RadioGroup>
        </FormItem>
        {/*  */}
        {showCustomize?(
          <FormItem  {...formItemLayout} label="">
            <RadioGroup onChange={this.handleSelectGroup2.bind(this)} style={{marginRight:"20px"}}>
              {customizeItem}
            </RadioGroup>
            {moreBtn && addBtnState?<Button type="primary" onClick={this.handleShowAddBox.bind(this)}><Icon type="plus-circle"/></Button>:null}
            
            {/* <Icon type="plus-circle"  style={{marginLeft:"20px",color:"#1890ff",fontSize:"20px"}}/> */}
          </FormItem>
        ):null}

        {addState?(
          <FormItem  {...tailFormItemLayout}>
            <Input onChange={this.handleNewGroupName.bind(this)} style={{width:"300px"}} addonAfter={
              <>
                <span style={{cursor:"pointer",marginRight:"20px",fontSize:"20px"}} onClick={this.handleAddGroup.bind(this)}><Icon style={{color:"#1890ff"}} type="check-circle" theme="filled" /></span>
                <span style={{cursor:"pointer",fontSize:"20px"}} onClick={this.handleCancelAddGroup.bind(this)}><Icon style={{color:"#f00"}}type="close-circle" theme="filled" /></span>
              </>
            }/>
          </FormItem>
        ):null}
        
        <FormItem  {...formItemLayout} label="">
          <TextArea autosize={{ minRows: 3 }} onChange={this.handleInput.bind(this, 'treatmentRemark')} />
        </FormItem>
        <FormItem  {...tailFormItemLayout}>
          {showErrorMessage()}
        </FormItem>
        <FormItem {...tailFormItemLayout}>
          <Button
            className="modal-btn"
            type="primary"
            onClick={this.handleSubmit.bind(this)}
            disabled={submitDisabled}
            loading={addSubmitLoading}
          ></Button>
          <Button className="modal-btn" onClick={this.handleAddPatientHide.bind(this)}></Button>
          {/* <Button className="modal-btn" onClick={this.handleChangeAddState.bind(this,1)}></Button> */}
        </FormItem>
      </div>
    )
    //
    const batchAdd = () => (
      <div>
        <div className="wx-words-refresh"><Icon type="sync" />&nbsp;</div>
        <Alert
          description={wxAddWords}
          type="info"
        />
        <div className="tips">*38</div>
        <div className='wx-add-btn'>
          <Button type="primary" className='wx-close-btn' onClick={this.handleAddPatientHide.bind(this)}></Button>
          <Button className='wx-close-btn' onClick={this.handleChangeAddState.bind(this, 0)}></Button>
        </div>
        <div className="teaching-link"><a href="#/" className="use-teaching"></a></div>
      </div>
    )
    //
    const addSuccess = () => (
      <div>
        <div className="invite-success-icon"><Icon type="check-circle" /></div>
        <div className="invite-success-wrods">{mobile}</div>
        <div className="return-to-input"><span onClick={this.handleChangeAddState.bind(this,0)} className="return-link"></span></div>
        <div className='wx-add-btn padding-bottom'>
          <Button type="primary" onClick={this.handleAddPatientHide.bind(this)}></Button>
        </div>
      </div>
    )
    //
    const addModalArray = [sigleAdd(), batchAdd(), addSuccess()]

    //
    const showUserItem = () => (
      <div className="user-item-wrap">
        <div className='user-item' onClick={this.handleUserCenterVisible.bind(this)}></div>
        {/* <div className='user-item' onClick={this.handleUpdatePhone.bind(this)}></div>
        <div className='user-item' onClick={this.handleChangePassword.bind(this)}></div> */}
        <div className='user-item' onClick={this.handleLogout.bind(this)}></div>
      </div>
    )

    const breadcrumbNameMap = {
      '/patient': '',
      '/patient/archives': '',
      '/plan': '',
      '/plan/measurement':"",
      '/plan/followup':"",
      '/plan/edit': '',
      '/plan/followup-edit': '',
      '/chat': '',
      '/crf': 'CRF',
      '/crf/patient': 'CRF',
      '/crf/patient/edit': '',
      '/user': ''
    };

    const { location } = this.props;

    const pathSnippets = location.pathname.split('/').filter(i => i);
    const extraBreadcrumbItems = pathSnippets.map((_, index) => {
      const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
      return (
        <Breadcrumb.Item key={url}>
          <Link to={url}>
            {breadcrumbNameMap[url]}
          </Link>
        </Breadcrumb.Item>
      );
    });
    const breadcrumbItems = [(
      <Breadcrumb.Item key="home">
        {/* <Link to="/patient"></Link> */}
      </Breadcrumb.Item>
    )].concat(extraBreadcrumbItems);

    const bindPatientBtn = (
      <div className='add-patient' onClick={this.handleAddPatientVisible.bind(this)}>
        <Icon className='icon' type="usergroup-add" title='' />
      </div>
    )
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Header style={{ padding: "0 20px" }}>
          <div className='header'>
            <div className='logo'>2</div>
            <div className='user'>
              {buttonAuth(getLocal('buttons') || [],'bindPatient',bindPatientBtn)}
              <div
                onClick={this.handleUserCenterVisible.bind(this)}
                className='user-info'
                //onMouseEnter={this.handleShowUserCenter.bind(this)}
                //onMouseLeave={this.handleHideUserCenter.bind(this)}
              >
                <img src={user.headUrl || defaultUser} alt='' />
                {userItem ? showUserItem() : null}
              </div>
              <div className='logout' onClick={this.handleLogout.bind(this)}>
                <Icon className='icon' type="logout" title=''/>
              </div>
            </div>
          </div>
        </Header>
        <Layout>
          <Sider
            width={200}
            theme="light"
            //collapsible
            collapsed={this.state.collapsed}
            onCollapse={this.onCollapse}
          >
            <MyMenu />
          </Sider>
          <Layout id="my-layout" style={{ padding: '0 24px 24px' }}>
            <div id="my-breadcrumb">
              <Breadcrumb style={{ margin: '16px 0' }} separator=">">
                  {breadcrumbItems}
              </Breadcrumb>
            </div>
            <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 300 }}>
              {this.props.content()}
            </Content>
          </Layout>
        </Layout>

        {/** */}
        <Modal
          visible={addPatientVisible}
          title=""
          onCancel={this.handleAddPatientHide.bind(this)}
          footer={null}
          width={700}
        >
          {addModalArray[addModalState]}
        </Modal>

        {/**  */}
        <Modal
          title=""
          visible={userCenterVisible}
          onCancel={this.handleUserCenterHide.bind(this)}
          footer={null}
          width={700}
        >
          <div className="user-center">
            <Row>
              <Col span={10} offset={2}>
                <div className="user-center-item">
                  <span></span>
                  <span></span>
                </div>
              </Col>
              <Col span={10} offset={2}>
                <div className="user-center-item">
                  <span></span>
                  <input value="" disabled />
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={10} offset={2}>
                <div className="user-center-item">
                  <span></span>
                  <input value="" disabled />
                </div>
              </Col>
              <Col span={10} offset={2}>
                <div className="user-center-item">
                  <span></span>
                  <input value="" disabled />
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={22} offset={2}>
                <div className="user-center-item">
                  <span></span>
                  <input className="user-address" value="" disabled />
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={22} offset={2}>
                <div className="user-center-item">
                  <span>13800138000</span>
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={22} offset={2}>
                <div className="user-center-item">
                  <span></span>
                  <span className="class-item"></span>
                  <span className="class-item"></span>
                  <span className="class-item"></span>
                  <span className="class-item"></span>
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={22} offset={2}>
                <div className="user-center-item">
                  <span></span>
                  <span className="user-image-wrap">
                    <div className="image-box"></div>
                    <div className="image-box"></div>
                  </span>
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={22} offset={2}>
                <div className="user-center-item">
                  <Button type="primary"></Button>
                </div>
              </Col>
            </Row>
          </div>
        </Modal>

        {/**  */}
        <Modal
          visible={updatePhoneVisible}
          title=""
          onCancel={this.handleUpdatePhoneHide.bind(this)}
          footer={null}
          width={700}
        >
          <Form>
            <FormItem {...formItemLayout} label="">
              <Input placeholder="" />
            </FormItem>
            <FormItem {...formItemLayout} label="">
              <Input
                placeholder=''
                addonAfter={<span onClick={this.handleGetCode.bind(this)} style={{ cursor: 'pointer' }}></span>}
              />
            </FormItem>
            <FormItem {...formItemLayout} label="">
              <Input placeholder="" />
            </FormItem>
            <FormItem {...tailFormItemLayout}>
              <Button type="primary"></Button>
            </FormItem>
          </Form>
        </Modal>

        {/**  */}
        <Modal
          visible={changePasswordVisible}
          title=""
          onCancel={this.handleChangePasswordHide.bind(this)}
          footer={null}
          width={700}
        >
          <Form>
            <FormItem {...formItemLayout} label="">
              <span>13800138000</span>
            </FormItem>
            <FormItem {...formItemLayout} label="">
              <Input
                placeholder=''
                type="password"
              />
            </FormItem>
            <FormItem {...formItemLayout} label="">
              <Input
                placeholder=""
                type="password"
              />
            </FormItem>
            <FormItem {...formItemLayout} label="">
              <Input
                placeholder=""
                type="password"
              />
            </FormItem>
            <FormItem {...tailFormItemLayout}>
              <Button type="primary"></Button>
            </FormItem>
          </Form>
        </Modal>

      </Layout>
    );
  }
}

const MyLayout = Form.create()(MyLayoutForm);

export default withRouter(MyLayout) 
