/**
 * 
 */
import React, { Component } from 'react';
import { Form, Select, Radio, Button, Row, Col, Input, DatePicker, InputNumber, Checkbox } from 'antd';
import { validChinese,validIntNumber } from '../../utils/formValidate'
import moment from 'moment';
const Option = Select.Option;
const FormItem = Form.Item;
const { RangePicker } = DatePicker;


class SaeForm extends Component {
    state = {

    }

    //
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (err) return;
            //
            this.props.onSubmit(values)
        });
    }

    render() {
        let formData = this.props.data ? this.props.data[0] : {}
        let {
            reportDate,
            initials,
            gender,
            age,
            saeName,
            situationFlag,
            situationDeathDate,
            situationOther,
            happenDate,
            learnDate,
            measureFlag,
            lapseFlag,
            researchMedicineRelation,
            deathHandleDetail
        } = formData
        const { getFieldDecorator, getFieldValue } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 20 },
            },
        };
        const formItemLayout2 = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        return (
            <div style={styles.wrap}>
                <FormItem
                    label=""
                    {...formItemLayout}
                >
                    {getFieldDecorator('reportDate', {
                        initialValue: reportDate?moment(reportDate):'',
                    })(
                        <DatePicker />
                    )}
                </FormItem>
                <FormItem
                    label=""
                    {...formItemLayout}
                >
                    {getFieldDecorator('initials', {
                        initialValue: initials,
                        rules:[{
                            validator:validChinese
                        }]
                    })(
                        <Input />
                    )}
                </FormItem>
                <FormItem
                    label=""
                    {...formItemLayout}
                >
                    {getFieldDecorator('gender', {
                        initialValue: gender,
                    })(
                        <Select>
                            <Option value={1}></Option>
                            <Option value={2}></Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    label=""
                    {...formItemLayout}
                >
                    {getFieldDecorator('age', {
                        initialValue: age,
                        rules:[{
                            validator:validIntNumber
                        }]
                    })(
                        <Input></Input>
                    )}
                </FormItem>
                <FormItem
                    label="SAE"
                    {...formItemLayout}
                >
                    {getFieldDecorator('saeName', {
                        initialValue: saeName,
                    })(
                        <Input />
                    )}
                </FormItem>
                <FormItem
                    label="SAE"
                    {...formItemLayout}
                >
                    {getFieldDecorator('situationFlag', {
                        initialValue: situationFlag,
                    })(
                        <Checkbox.Group className="inline-item">
                            <Checkbox value=""></Checkbox>
                            {
                                getFieldValue('situationFlag') && getFieldValue('situationFlag').indexOf('') >= 0 ? <FormItem style={{'margin':'0 10px 0 0'}} className="inline-item">
                                    {getFieldDecorator('situationDeathDate', {
                                        initialValue: situationDeathDate?moment(situationDeathDate):'',
                                    })(
                                        <DatePicker />
                                    )}
                                </FormItem> : null
                            }
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                        </Checkbox.Group>
                    )}
                    {
                        getFieldValue('situationFlag') && getFieldValue('situationFlag').indexOf('') >= 0 ? <FormItem style={{'margin':'0 10px 0 0'}} className="inline-item">
                            {getFieldDecorator('situationOther', {
                                initialValue: situationOther,
                            })(
                                <Input />
                            )}
                        </FormItem> : null
                    }
                    {
                        getFieldValue('situationFlag') && getFieldValue('situationFlag').indexOf('') >= 0 ? <div>
                            <FormItem
                                label=""
                                {...formItemLayout2}
                            >
                                {getFieldDecorator('happenDate', {
                                    initialValue: happenDate?moment(happenDate):'',
                                })(
                                    <DatePicker />
                                )}
                            </FormItem>
                            <FormItem
                                label="SAE"
                                {...formItemLayout2}
                            >
                                {getFieldDecorator('learnDate', {
                                    initialValue: learnDate?moment(learnDate):'',
                                })(
                                    <DatePicker />
                                )}
                            </FormItem>
                            <FormItem
                                label=""
                                {...formItemLayout2}
                            >
                                {getFieldDecorator('measureFlag', {
                                    initialValue: measureFlag ? measureFlag.split('') : [],
                                })(
                                    <Checkbox.Group>
                                        <Checkbox value=""></Checkbox>
                                        <Checkbox value=""></Checkbox>
                                        <Checkbox value=""></Checkbox>
                                        <Checkbox value=""></Checkbox>
                                    </Checkbox.Group>
                                )}
                            </FormItem>
                        </div> : null
                    }
                </FormItem>
                <FormItem
                    label="SAE"
                    {...formItemLayout}
                >
                    {getFieldDecorator('lapseFlag', {
                        initialValue: lapseFlag ? lapseFlag.split('') : [],
                    })(
                        <Checkbox.Group>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                        </Checkbox.Group>
                    )}
                </FormItem>
                <FormItem
                    label="SAE"
                    {...formItemLayout}
                >
                    {getFieldDecorator('researchMedicineRelation', {
                        initialValue: researchMedicineRelation ? researchMedicineRelation.split('') : [],
                    })(
                        <Checkbox.Group>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                            <Checkbox value=""></Checkbox>
                        </Checkbox.Group>
                    )}
                </FormItem>
                <FormItem
                    label="SAE"
                    {...formItemLayout}
                >
                    {getFieldDecorator('deathHandleDetail', {
                        initialValue: deathHandleDetail,
                    })(
                        <Input.TextArea></Input.TextArea>
                    )}

                </FormItem>
            </div>
        )
    }
}

const styles = {
    wrap: {
        padding: '10px',
        border: '1px solid #ccc'
    }
}

export default SaeForm