/**
 * 
 */
import React, { Component } from 'react';
import { Form, Button, Input } from 'antd';
import { validDoubleNumber } from '../../utils/formValidate'
const FormItem = Form.Item;

class Module4 extends Component {

    //
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (err) return;
            //
            this.props.onSubmit(values)
        });
    }

    render() {
        let {
            systolicPressure,
            diastolicPressure,
            heartRate,
            weight,
            height,
            bmi,
            waistline,
            hipline
        } = this.props.formData;
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 3},
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
        };
        return (
            <div>
                <div className="title"></div>
                <Form labelAlign="left" {...formItemLayout} onSubmit={this.handleSubmit.bind(this)}>
                    <FormItem label="">
                        <FormItem className="inline-item">
                            {
                                getFieldDecorator('systolicPressure', {
                                    initialValue: systolicPressure,
                                    rules:[{
                                        validator:validDoubleNumber
                                    }]
                                })(
                                    <Input className="small-input" />
                                )
                            }
                        </FormItem>
                        <span>/</span>
                        <FormItem className="inline-item">
                            {
                                getFieldDecorator('diastolicPressure', {
                                    initialValue: diastolicPressure,
                                    rules:[{
                                        validator:validDoubleNumber
                                    }]
                                })(
                                    <Input addonAfter="mmHg" className="cover-input"/>
                                )
                            }
                        </FormItem>
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('heartRate', {
                                initialValue: heartRate,
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonAfter="/" />
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('weight', {
                                initialValue: weight,
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonAfter="kg" />
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('height', {
                                initialValue: height,
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonAfter="cm" />
                            )
                        }
                    </FormItem>
                    <FormItem label="BMI">
                        {
                            getFieldDecorator('bmi', {
                                initialValue: bmi,
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonAfter="kg/m2" />
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('waistline', {
                                initialValue: waistline,
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonAfter="cm" />
                            )
                        }
                    </FormItem>
                    <FormItem label="">
                        {
                            getFieldDecorator('hipline', {
                                initialValue: hipline,
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonAfter="cm" />
                            )
                        }
                    </FormItem>
                </Form>
                {
                    this.props.canSave ? <div className="btn-wrap">
                        <Button type="primary" disabled={this.props.disabled} onClick={this.handleSubmit.bind(this)}></Button>
                        <Button onClick={this.props.onCancel}></Button>
                    </div> : null
                }
            </div>
        )
    }
}

const ThisForm = Form.create({
    onValuesChange:(props, changedValues, allValues) => {
        if(!props.canSave){
            props.setCanSave(true)
        }
    }
})(Module4);

export default ThisForm