/**
 * -4
 */
import React, { Component } from 'react';
import { Form, Radio, Button, Input, DatePicker } from 'antd';
import moment from 'moment';
import { validDoubleNumber } from '../../utils/formValidate'
const FormItem = Form.Item;

class Module11 extends Component {
    state = {

    }

    //
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (err) return;
            //
            this.props.onSubmit(values)
        });
    }

    getDisabledDate(date) {
        if(date.valueOf()-new Date().getTime()>0){
            return false;
        }else{
            return true;
        }
    }


    render() {
        let {
            relieveFlag,
            medicineMelbineDosage,
            other,
            expectedFollowDate
        } = this.props.formData;
        const { getFieldDecorator, getFieldValue } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        return (
            <div>
                <div className="title"></div>
                <Form labelAlign="left" {...formItemLayout} onSubmit={this.handleSubmit.bind(this)}>
                    <FormItem label="">
                        {
                            getFieldDecorator('relieveFlag', {
                                initialValue: relieveFlag,
                            })(
                                <Radio.Group>
                                    <Radio value={true}></Radio>
                                    <Radio value={false}></Radio>
                                </Radio.Group>
                            )
                        }
                        {
                            !getFieldValue('relieveFlag') ? <span>
                                <span></span>
                                <FormItem
                                    className="inline-item"
                                >
                                    {getFieldDecorator('medicineMelbineDosage', {
                                        initialValue: medicineMelbineDosage,
                                        rules:[{
                                            validator:validDoubleNumber
                                        }]
                                    })(
                                        <Input style={styles.formInput} />
                                    )}
                                </FormItem>
                            </span> : null
                        }
                    </FormItem>

                   
                    <FormItem
                        label=""
                    >
                        {getFieldDecorator('other', {
                            initialValue: other,
                        })(
                            <Input className="big-input" />
                        )}
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        {getFieldDecorator('expectedFollowDate', {
                            initialValue: expectedFollowDate?moment(expectedFollowDate):'',
                        })(
                            <DatePicker disabledDate={this.getDisabledDate.bind(this)} />
                        )}
                    </FormItem>
                </Form>
                {
                    this.props.canSave ? <div className="btn-wrap">
                        <Button disabled={this.props.disabled} type="primary" onClick={this.handleSubmit.bind(this)}></Button>
                        <Button onClick={this.props.onCancel}></Button>
                    </div> : null
                }
            </div>
        )
    }
}

const styles = {
    wrap: {
        marginTop: "50px",
    },
    title: {
        fontSize: "18px",
        borderLeft: "4px solid #1890ff",
        paddingLeft: "10px"
    },
}

const ThisForm = Form.create({
    onValuesChange:(props, changedValues, allValues) => {
        if(!props.canSave){
            props.setCanSave(true)
        }
    }
})(Module11);

export default ThisForm