/**
 * 
 */
import React, { Component } from 'react';
import { Form, Button, Input, Table } from 'antd';
import { validDoubleNumber } from '../../utils/formValidate'
const FormItem = Form.Item;

class Module4 extends Component {
    //
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (err) return;
            //
            this.props.onSubmit(values)
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const renderContent = (value, row, index) => {
            const obj = {
                children: value,
                props: {},
            };
            if (index === 4) {
                obj.props.colSpan = 0;
            } else {
                obj.children = <FormItem>
                    {
                        getFieldDecorator(value, {
                            initialValue:this.props.formData[value],
                            rules:[{
                                validator:validDoubleNumber
                            }]
                        })(
                            <Input className="middle-input" />
                        )
                    }
                </FormItem>
            }
            return obj;
        };

        const columns = [{
            title: '',
            dataIndex: 'time',
            render: (text, row, index) => {
                if (index < 4) {
                    return text;
                }
                text = <div>
                    <FormItem label="">
                        {
                            getFieldDecorator('uae1', {
                                initialValue:this.props.formData['uae1'],
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonBefore="1" addonAfter="g/min" className="cover-input" />
                            )
                        }
                    </FormItem>
                    <span style={styles.space}></span>
                    <FormItem>
                        {
                            getFieldDecorator('uae2', {
                                initialValue:this.props.formData['uae2'],
                                rules:[{
                                    validator:validDoubleNumber
                                }]
                            })(
                                <Input addonBefore="2" addonAfter="g/min" className="cover-input" />
                            )
                        }
                    </FormItem>
                </div>
                return {
                    children: text,
                    props: {
                        colSpan: 4,
                    },
                };
            },
        }, {
            title: ' mmol/L',
            dataIndex: 'key1',
            render: renderContent,
        }, {
            title: '(U/ml)',
            dataIndex: 'key2',
            render: renderContent
        }, {
            title: '(U/ml)',
            dataIndex: 'key3',
            render: renderContent,
        }];

        const data = [{
            key: '1',
            time: '0 min',
            key1: 'bs0',
            key2: 'insulin0',
            key3: 'cp0'
        }, {
            key: '2',
            time: '30min',
            key1: 'bs30',
            key2: 'insulin30',
            key3: 'cp30'
        }, {
            key: '3',
            time: '60min',
            key1: 'bs60',
            key2: 'insulin60',
            key3: 'cp60'
        }, {
            key: '4',
            time: '120min',
            key1: 'bs120',
            key2: 'insulin120',
            key3: 'cp120'
        }, {
            key: '5',
            key1: 'key111'
        }];
        return (
            <div>
                <div className="title"></div>
                <Form layout="inline" onSubmit={this.handleSubmit.bind(this)}>
                    <Table columns={columns} dataSource={data} bordered pagination={false} />
                    {
                        this.props.canSave ? <div className="btn-wrap">
                            <FormItem>
                                <Button disabled={this.props.disabled} type="primary" htmlType="submit"></Button>
                                <Button onClick={this.props.onCancel}></Button>
                            </FormItem>
                        </div> : null
                    }
                </Form>
            </div>
        )
    }
}

const styles = {
    space:{
        margin: '0 10px'
    }
}

const ThisForm = Form.create({
    onValuesChange:(props, changedValues, allValues) => {
        if(!props.canSave){
            props.setCanSave(true)
        }
    }
})(Module4);

export default ThisForm